#gsp-sidebar-menu-container nav {
  width: 80%;
}

/* mobile menu */

.nav-primary-menu {
  display: block;
  min-height: 100%;
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
}

.menu-closer-box {
  min-height: 100%;
  width: 20%;
  cursor: pointer;
  --tw-bg-opacity: 1;
  background-color: rgb(209 213 219 / var(--tw-bg-opacity));
  opacity: 0.8;
}

.nav-item {
  padding: 0.5rem;
}

@media (min-width: 768px) {
  .nav-item {
    min-width: 4rem;
    border-top-right-radius: 0.375rem;
    border-top-left-radius: 0.375rem;
    padding-left: 0px;
    padding-right: 0px;
  }
}

/* mobile menu */

#gsp-sidebar-menu-container .item-child {
  display: block;
}

#gsp-sidebar-menu-container .item-child:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgb(0 0 0 / var(--tw-text-opacity));
}

@media (min-width: 768px) {
  #gsp-sidebar-menu-container .item-child {
    min-width: 4rem;
    padding-left: 0.5rem;
  }
}

.item-parent {
  position: relative;
}

.item-parent:hover {
  padding-left: 0px;
  padding-right: 0px;
}

/* desktop menu*/

#gsp-header-menu .item-parent {
  display: flex;
  min-width: 5rem;
  justify-content: space-between;
}

.item-parent a {
  display: flex;
  justify-content: space-between;
}

/* mobile & desktop */

.item-parent .sub-menu {
  z-index: 50;
  height: auto;
  min-width: 100%;
}

@media (min-width: 768px) {
  .item-parent .sub-menu {
    position: absolute;
    left: 0px;
    height: auto;
    min-width: 13rem;
  }
}

/* desktop */

#gsp-header-menu .item-parent .sub-menu {
  --tw-bg-opacity: 1;
  padding-left: 0.5rem;
}

@media (min-width: 768px) {
  #gsp-header-menu .item-parent .sub-menu {
    left: 0px;
    height: auto;
    min-width: 13rem;
  }
}

